interface ShipImprovement {
  fire: number;
  torpedo: number;
  antiAir: number;
  armor: number;
  luck: number;
  hp: number;
  asw: number;
}

/**
 * 
 * @export
 * @class ShipStock
 */
export default class ShipStock {
  /** id */
  public id = 0;

  /** ID  */
  public uniqueId = 0;

  /**  */
  public level = 1;

  /**  */
  public exp = 0;

  /**  */
  public improvement = { hp: 0, asw: 0, luck: 0 } as ShipImprovement;

  /**  */
  public releaseExpand = false;

  /**  */
  public area = 0;
}
